import React, { useEffect, useRef, useState } from 'react'
import { Card, Container } from 'react-bootstrap'
import Form from 'react-bootstrap/Form';
import './bill.css'
import { useReactToPrint } from 'react-to-print';

function Fullbill(props) {
    const { bills, fetchingBills, removeBill } = props;
    const [ifinstallment, setIfInstallment] = useState(false)
    const [discount, setDiscount] = useState(0)
    const [payed, setPayed] = useState()
    const [remains, setRemains] = useState(0)
    const cashRef = useRef();
    const installmentRef = useRef();


    let totalProdsPrice = 0;
    let totalCounts = 0

    bills.length >= 1 && bills.map(bill => {
        return (
            totalProdsPrice += bill.price * bill.quantity,
            totalCounts += bill.quantity
        )

    });

    let totalAfterDisc = totalProdsPrice - discount;
    let Theremain = totalProdsPrice - payed;

    const componentRef = useRef();

    const handlPrint = useReactToPrint({
        content: () => componentRef.current,
        documentTitle: 'emp-data',
        onAfterPrint: () => alert('print success')
    })

    return (
        <>
            <div style={{ padding: "40px 0" }} ref={componentRef}>

                <Container> <div className='mt-3 mb-3'>
                    <div>  :</div>
                    {bills && bills.map((bill, index) => {
                        return (
                            <div key={bill.id}>

                                {` ${index + 1}) `}  : <span style={{ fontWeight: "bold" }}>{bill.buyer_name}</span>/   : <span style={{ fontWeight: "bold" }}>{bill.seller_name}</span>/   : <span style={{ fontWeight: "bold" }}>{bill.bill_date}</span>/
                            </div>
                        )
                    })}
                </div>
                    <table className='text-center w-100 table table-striped'>
                        <thead>
                            <tr>
                                <th>#</th>
                                <th> </th>
                                <th> </th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th> </th>
                                <th></th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {bills && bills.map((bill, index) => {
                                return (
                                    <tr key={bill.id}>
                                        <td>{index + 1}</td>
                                        <td>{bill.item_code}</td>
                                        <td>{bill.item_name}</td>
                                        <td>{bill.price} .</td>
                                        <td>{bill.quantity}</td>
                                        <td>{bill.price * bill.quantity} .</td>
                                        <td>{bill.house}</td>
                                        <td>{bill.discount}</td>
                                        <td>{bill.price * bill.quantity} .</td>
                                        <td>{bill.bill_date}</td>
                                        <td onClick={() => removeBill(bill.id)} style={{ cursor: "pointer" }}>X</td>
                                    </tr>
                                )
                            })}

                        </tbody>
                    </table></Container >
                <Container >
                    <div className='bill-container row justify-content-between'>
                        <div className='bill-card m-auto col-lg-5 col-sm-12 col-12'>
                            <div className='card-p card-title d-flex justify-content-between align-items-center'>
                                <p>  </p>
                                <p> {totalProdsPrice} .</p>
                            </div>
                            <div className='card-p card-quants cardBody justify-content-between align-items-center'>
                                <div className='cardBodychild d-flex d-flex justify-content-between align-items-center'>
                                    <div> </div>
                                    <div>{totalCounts}</div>
                                </div>
                                <div className='cardBodychild d-flex d-flex justify-content-between align-items-center'>
                                    <div> </div>
                                    <div>{bills.length}</div>
                                </div>
                            </div>
                            <div className='card-p d-flex justify-content-between align-items-center'>
                                <div> </div>
                                <div>
                                    <label htmlFor='cash'> </label>
                                    <input
                                        ref={cashRef}
                                        type='radio'
                                        id='cash'
                                        name='payment'
                                    />
                                </div>
                                <div>
                                    <label htmlFor='installment'> </label>
                                    <input ref={installmentRef} type='radio' id='installment' name='payment' />
                                </div>
                            </div>
                            <div className='card-p d-flex justify-content-between align-items-center'>
                                <div>
                                    <label className='mb-2' htmlFor='priceperone'> (.): </label>
                                    <Form.Control
                                        onChange={(e) => setDiscount(e.target.value)}
                                        style={{ height: '25px' }}
                                        type='number'
                                        id='priceperone' />
                                </div>

                                <div>
                                    <label className='mb-2' htmlFor='priceperone'>  :</label>
                                    <Form.Control
                                        style={{ height: '25px' }}
                                        type='text-area'
                                        id='priceperone' />
                                </div>
                            </div>
                        </div>
                        <div className='bill-card m-auto col-lg-5 col-sm-12 col-12'>
                            <div className='card-p card-title d-flex justify-content-between align-items-center'>
                                <p>  </p>
                                <p> {totalAfterDisc} .</p>
                            </div>
                            <div className='card-p  d-flex justify-content-between align-items-center'>
                                <div>
                                    <label className='mb-2' htmlFor='priceperone'>  :</label>
                                    <Form.Control
                                        onChange={(e) => setPayed(e.target.value)}
                                        style={{ height: '25px' }}
                                        type='number'
                                        id='priceperone' />
                                </div>
                                <div>
                                    <label className='mb-2' htmlFor='remains'>  :</label>
                                    <Form.Control
                                        value={Theremain}
                                        disabled={true}
                                        style={{ height: '25px' }}
                                        type='number'
                                        id='remains' />
                                </div>
                            </div>
                            <div className='card-p d-flex justify-content-between align-items-center'>
                                <div>
                                    <input type='checkbox' id='taxbill' name='payment' />
                                    <label className='m-2' htmlFor='taxbill'>  </label>
                                </div>
                                <div>
                                    <label className='mb-2' htmlFor='taxnum'>    :</label>
                                    <Form.Control
                                        style={{ height: '25px' }}
                                        type='number'
                                        id='taxnum' />
                                </div>
                            </div>

                        </div>
                    </div>
                </Container >

            </div >
            <div className='w-100 mb-3 mt-3 text-center'>
                <button onClick={handlPrint}>print</button>
            </div>
        </>
    )
}

export default Fullbill